.ProductsContainer {
    max-width: 1000px;
    display: inline-flex;
    flex-wrap: wrap;
    justify-content: center;
    padding-bottom: 60px;
}

.ProductThumbnail {
    width: 30%;
    margin: 10px;
    border: 1px solid lightgrey;
    border-radius: 6px;
    overflow: hidden;
}

.ProductImage {
    height: auto;
    width: auto;
    max-height: 100%;
    max-width: 100%;    
}

.ProductDescription {
    padding: 10px;
}


@media (max-width: 700px) {
    .ProductThumbnail {
        width: 90%;
        margin: 5%;    
    }
}