@import '../styles/Colors.css';

.navbar {
    background-color: var(--charcoal);
    padding: 40px;
    user-select: none;
    -moz-user-select: none;
    -webkit-user-select: none;
    -webkit-user-drag: none;
}


.navbarContainer {
    margin: auto;
    max-width: 1300px;
    color: var(--white);
    display: flex;
    align-items: center;
}



.navbarContainer > a:last-child {
    text-align: right;
}

.navLinks {
    flex-grow: 1;
}

.navLinks > a {
    margin: 10px;
}

.active {
    color: var(--green);
}


.TextLogo {
    text-align: left;
    font-size: 2.5rem;
    white-space: nowrap;
}

.navLinks > a:hover {
    color: var(--green);
}

.navLinks {
    padding: 10px 0;
}

@media (min-width: 650px) {
    .navbarContainer > a:first-child,
    .navbarContainer > a:last-child {
        width: 25%;
    }    
}

@media (max-width: 650px) {
    .navbar {
        padding-bottom: 10px;
    }

    .navbarContainer {
        flex-direction: column;
    }

    .navbarContainer > a:last-child {
        position: absolute;
        right: 25px;
        top: 50px;
    }
}

@media (min-width: 650px) and (max-width: 900px) {
    .navLinks {
        text-align: right;
    }
}