/* Global styles here */
html,
body {
    margin: 0;
    padding: 0;
    font-family: 'Great Vibes', cursive;
    text-align: center;
}

p {
    font-family: 'Raleway', sans-serif;
}

* {
    box-sizing: border-box;
    user-select: none;
    -moz-user-select: none;
    -webkit-user-select: none;
    -webkit-user-drag: none;
}

a {
    color: inherit;
    text-decoration: inherit;
    transition-duration: 200ms;
}

a:hover {
    cursor: pointer;
}

.appContainer {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}

.appContainer > main {
    flex-grow: 1;
}

.PageTitle {
    font-size: 3.5rem;
}

.Button {
    padding: 4px 15px;
    border: 1px solid black;
    border-radius: 5px;
    cursor: pointer;
}

.Button:hover,
.QtyAdjustmentButton:hover {
    background-color: var(--charcoal);
    color: var(--white);
}

.QtyAdjustmentButton {
    border: 1px solid black;
    cursor: pointer;
    border-radius: 10px;
    padding: 5px;
    margin: 10px;
}